<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 商家上新货物
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card>
            <el-button type="primary" style="margin-bottom: 10px;" @click="addGoods">新增货物</el-button>
            <el-table
                    :data="tableData">
                <el-table-column
                        prop="goodId"
                        label="货物Id">
                </el-table-column>
                <el-table-column
                        prop="goodName"
                        label="货物名称">
                </el-table-column>
                <el-table-column
                        prop="goodPrice"
                        label="单价">
                </el-table-column>
                <el-table-column
                        prop="goodStatus"
                        label="租赁类型">
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!-- 编辑弹出框 -->
            <el-dialog title="新增货物" :visible.sync="editVisible" width="30%">
                <el-form ref="form" :model="form" label-width="70px">
                    <el-form-item label="货物名称">
                        <el-input v-model="form.goodName"></el-input>
                    </el-form-item>
                    <el-form-item label="租赁单价">
                        <el-input v-model="form.goodPrice"></el-input>
                    </el-form-item>
                    <el-form-item label="租赁模式">
                        <el-dropdown  @command="handleCommand">
                            <el-button type="primary">
                                {{buttonName}}
                                <i class="el-icon-arrow-down el-icon--right" ></i>
                            </el-button>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command="1">先租后买</el-dropdown-item>
                                <el-dropdown-item command="2">以租代售</el-dropdown-item>
                                <el-dropdown-item command="3">共享租赁</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </el-form-item>
                    <el-form-item label="货物描述">
                        <el-input v-model="form.goodDescription"></el-input>
                    </el-form-item>

                </el-form>
                <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
            </el-dialog>

            <el-dialog
                    title="修改货物信息"
                    :visible.sync="dialogVisible"
                    width="30%">
                <el-form ref="form2" :model="form2" label-width="70px">
                    <el-form-item label="货物名称">
                        <el-input v-model="form2.goodName"></el-input>
                    </el-form-item>
                    <el-form-item label="租赁单价">
                        <el-input v-model="form2.goodPrice"></el-input>
                    </el-form-item>
                    <el-form-item label="租赁模式">
                        <el-dropdown  @command="handleCommand2">
                            <el-button type="primary">
                                {{buttonName2}}
                                <i class="el-icon-arrow-down el-icon--right" ></i>
                            </el-button>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command="1">先租后买</el-dropdown-item>
                                <el-dropdown-item command="2">以租代售</el-dropdown-item>
                                <el-dropdown-item command="3">共享租赁</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </el-form-item>
                    <el-form-item label="货物描述">
                        <el-input v-model="form2.goodDescription"></el-input>
                    </el-form-item>

                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="editGoods">确 定</el-button>
                </span>
            </el-dialog>
        </el-card>
    </div>
</template>

<script>
    export default {
        name: "bussinessGoods",
        data() {
            return {
                tableData: [],
                editVisible:false,
                dialogVisible:false,
                form:{},
                form2:{},
                status:'',
                buttonName:'先租后买',
                buttonName2:'先租后买'
            }
        },
        created() {
            this.getAllGoods()
        },
        methods:{
            addGoods(){
                this.editVisible=true
            },
            getUUID() {
                return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
            },
            saveEdit(){
                this.$confirm('确定要新增吗？', '提示', {
                    type: 'warning'
                })
                    .then(() => {
                        if(this.buttonName == '先租后买'){
                            this.status = 1
                        }else if(this.buttonName == '以租代售'){
                            this.status = 2
                        }else {
                            this.status = 3
                        }
                        let user = localStorage.getItem('ms_username')
                        let uuid = (this.getUUID()+this.getUUID()+"-"+this.getUUID()+"-"+this.getUUID()+"-"+this.getUUID()+"-"+this.getUUID()+this.getUUID()+this.getUUID());
                        this.form.goodNum = uuid
                        this.form.goodStatus = this.status
                        this.form.goodOwner = user
                        this.$http.post('/good/save',this.form)
                        .then((data)=>{
                            if(data.status == '200'){
                                this.$message.success('新增成功')
                                this.getAllGoods()
                                this.editVisible = false

                            }else{
                                this.$message.error('新增失败')
                            }
                            console.log(data);
                        })
                        .catch((err)=>console.log(err))
                    })
                    .catch(() => {
                    });
            },
            handleCommand(command) {
                // this.status = command
                console.log(command);
                if(command == 1){
                    this.buttonName = '先租后买'
                }else if(command == 2){
                    this.buttonName = '以租代售'
                }else{
                    this.buttonName = '共享租赁'
                }
            },
            handleCommand2(command) {
                if(command == 1){
                    this.buttonName2 = '先租后买'
                }else if(command == 2){
                    this.buttonName2 = '以租代售'
                }else{
                    this.buttonName2 = '共享租赁'
                }
            },
            getAllGoods(){
                this.tableData = []
                let user = localStorage.getItem('ms_username')
                this.$http.get('/good/findGoodsByOwner?goodOwner='+user)
                .then((data)=>{
                    console.log(data);
                    data.data.forEach(item=>{
                        if(item.goodStatus == '1'){
                            item.goodStatus = '先租后买'
                        }else if(item.goodStatus == 2){
                            item.goodStatus = '以租代售'
                        }else if(item.goodStatus == 3){
                            item.goodStatus = '共享租赁'
                        }else{
                            item.goodStatus = '空'
                        }
                    })
                    data.data.forEach(item=>{
                        if(item.goodStatus!='空'){
                            this.tableData.push(item)
                        }
                    })
                    // this.tableData = data.data
                    console.log(data);
                })
                .catch((err)=>console.log(err))
            },
            handleEdit(index,row){
                this.dialogVisible = true
                this.form2.goodNum = row.goodNum
                this.form2.goodId = row.goodId
                this.form2.goodDescription = row.goodDescription
                // this.form2.goodName = row.goodName
                // this.form2.goodPrice = row.goodPrice
                // this.buttonName2 = row.goodStatus
            },
            handleDelete(index,row){
                this.$confirm('确定要删除吗？', '提示', {
                    type: 'warning'
                })
                    .then(() => {
                        console.log(row);
                        this.$http.get('/good/delete?goodId='+row.goodId)
                        .then((data)=>{
                            if(data.status == '200'){
                                this.$message.success('删除成功')
                                this.tableData.splice(index, 1);
                            }else{
                                this.$message.error('删除失败')
                            }
                        })
                    })
                    .catch(()=>{})
            },
            editGoods(){
                if(this.buttonName2 == '先租后买'){
                    this.form2.goodStatus = 1
                }else if(this.buttonName2 == '以租代售'){
                    this.form2.goodStatus = 2
                }else{
                    this.form2.goodStatus = 3
                }
                this.$http.post('/good/save',this.form2)
                .then((data)=>{
                    if(data.status == '200'){
                        this.dialogVisible = false;
                        this.getAllGoods()
                        this.$message.success('修改成功');
                    }
                })
                .catch((err)=>{
                    console.log(err);
                })
            }
        }
    }
</script>

<style scoped>

</style>